import React, { useState, useEffect } from 'react';

const LoadingDots = () => {
  const [dots, setDots] = useState('');

  useEffect(() => {
    const interval = setInterval(() => {
      setDots((prevDots) => {
        if (prevDots.length === 3) {
          return '.';
        }
        return prevDots + '.';
      });
    }, 400);

    return () => clearInterval(interval);
  }, []);

  return <>{dots}</>;
};

export default LoadingDots;